import { React, useState } from "react";
import axios from "axios";
import AlertMsg from "./AlertMsg";

const ForgetPwd = ({ setIsForget, isForget }) => {
  const [userMail, setUserMail] = useState(null);
  const [showAlert, setShowAlert] = useState(false);
  const [showAlertErr, setShowAlertErr] = useState(false);
  const [msg, setMsg] = useState("");

  const sendForgetMail = async (e) => {
    e.preventDefault();
    if (!userMail) {
      setMsg("");
      setShowAlertErr(true);
    }

    try {
      await axios.post("http://localhost:4107/book/forget-password", {
        userMail,
      });
      setShowAlert(true);
      setMsg("");
    } catch (err) {
      if (err.response.status === 404) {
        setMsg("");
        setShowAlertErr(true);
      }
    }
  };
  return (
    <div
      className="bookAlertBg"
      onClick={(e) =>
        e.target.className === "bookAlertBg" ? setIsForget(false) : null
      }
    >
      <form className="forgetAlert">
        <p className="fs-6  mb-3 text-center">
          
        </p>
        <div className="d-flex justify-content-center align-items-center">
          <label> </label>
          <input
            type="email"
            placeholder="E-mail"
            className="px-2 w-75"
            onInput={(e) => {
              setUserMail(e.target.value);
            }}
            required
          />
        </div>
        <button className="bookAlertBtn" onClick={sendForgetMail}>
          
        </button>
        {showAlert && (
          <AlertMsg
            msg={msg}
            close={() => {
              setShowAlert(false);
              window.location.replace("http://localhost:3000");
            }}
            showAlert={showAlert}
            setShowAlert={setShowAlert}
          />
        )}
        {showAlertErr && (
          <AlertMsg
            msg={msg}
            close={(e) => {
              e.preventDefault();
              setShowAlertErr(false);
            }}
            showAlert={showAlert}
            setShowAlert={setShowAlert}
          />
        )}
      </form>
    </div>
  );
};

export default ForgetPwd;
