import React, { useEffect, useState } from "react";
import axios from "axios";
import { useParams, Link } from "react-router-dom";
import "../../components/member/member.css";
import MemberDone from "../../components/dashboard/MemberDone";
import { useAttendance } from "../../components/dashboard/useAttendance";
import ControllAccordion from "../../components/dashboard/ControllAccordion";

const Member = () => {
  const { ornumber } = useParams();
  const [orderData, setOrderData] = useState({});
  const { attdata } = useAttendance({ ornumber:ornumber})
  const {
    userid,
    orname,
    oremail,
    orphone,
    orcity,
    orrural,
    oraddress,
    money,
    pay,
    ordertime,
    orderdone,
    state,
    note,
    employeeid,
    date,
    time,
    weeks,
    donetime,
    employeename,
    employeephone,
    employeeemail,
  } = orderData;

  

  //
  useEffect(() => {
    async function fetchData() {
      try {
        const result = await axios.get(
          `http://localhost:4107/AdminOrder/${ornumber}`
        );
        setOrderData(result.data[0]);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    }
    fetchData();
  }, [ornumber,state]);

  const handleOrderStatus = (state) => {
    if (state === 0) {
      return "";
    } else if (state === 1) {
      return "";
    }
    return "";
  };

  const handleTime = (time) => {
    if (time === 0) {
      return "08:00";
    } else if (time === 1) {
      return "13:00";
    }
    return "18:00";
  };

  return (
    <div>
      <div className="Container">
        <h3 className="orderh3"></h3>
        <div
          className={`orderContainer bgdone ${state === 2 ? "complete" : ""}`}
        >
          <div className="orderContent row">
            <div className="col" style={{
              fontSize: "30px"}}>
              <span style={{ color: "red"}}>{donetime}/</span>
              <span>{weeks}</span>
              <p>{handleOrderStatus(state)}</p>
            </div>
            <table border={1} className="col">
              <tr>
                <th style={{ fontWeight: "600" }}>
                  <Link
                    to={`/dashboard/PersonalInfo/${userid}`}
                    className="Link-decoration"
                  >
                    
                  </Link>
                </th>
              </tr>
              <tr>
                <td>:{userid}</td>
              </tr>
              <tr>
                <td>:{orname}</td>
              </tr>
              <tr>
                <td>:{orphone}</td>
              </tr>
              <tr>
                <td>Email:{oremail}</td>
              </tr>
            </table>
            <table border={1} className="col">
              <tr>
                <th style={{ fontWeight: "600" }}>
                  <Link
                    to={`/dashboard/StaffList/${employeeid}`}
                    className="Link-decoration"
                  >
                    
                  </Link>
                </th>
              </tr>
              <tr>
                <td>:{employeeid}</td>
              </tr>
              <tr>
                <td>:{employeename}</td>
              </tr>
              <tr>
                <td>:{employeephone}</td>
              </tr>
              <tr>
                <td>Email:{employeeemail}</td>
              </tr>
            </table>
          </div>
          {/*  */}
          <table border={1}>
            <tr>
              <th style={{ fontWeight: "600" }}></th>
              <th></th>
            </tr>
            <tr>
              <td>:{ornumber}</td>
              <td>:{new Date(date).toLocaleDateString("en-CA")}</td>
            </tr>
            <tr>
              <td>:{handleTime(time)}</td>
              <td>:{weeks}</td>
            </tr>
            <tr>
              <td>:{pay ? "" : ""}</td>
              <td>:{money}</td>
            </tr>
            <tr>
              <td>:{donetime}</td>
              <td>:{orcity + orrural + oraddress}</td>
            </tr>
            <tr>
              <td>:{new Date(ordertime).toLocaleString("en-CA")}</td>
              <td>
                :
                {orderdone ? new Date(orderdone).toLocaleString("en-CA") : ""}
              </td>
            </tr>
          </table>
          <div className="orderContent">:{note || ""}</div>
          {attdata?.length !== 0 && <div className="ControllAccordion col-12" style={{ overflow: "auto" }}>
                <ControllAccordion items={attdata} Accordion={MemberDone}  />
            </div> }
        </div>
      </div>
    </div>
  );
};

export default Member;
