import "./dashboard.css";

function StaffAccordion({ item, active, onClick }) {
  const {
    ornumber,
    employeeid,
    efficiency, //
    clean, //
    careful, //
    manner, //
    reply, //
    orphone,
    oremail,
    orcity,
    orrural,
    oraddress,
    uid,
    orname,
    money,
    pay,
    ordertime,
    orderdone,
    state
  } = item;

  // 
  const handleOrderStatus = (state) => {
    if (state === "0") {
      return "";
    } else if (state === "1") {
      return "";
    }
    return "";
  };

  // 
  const ordt = new Date(ordertime).toLocaleString()
  const ordd =orderdone? new Date(orderdone).toLocaleString():null

  return (
    <div
      className={`staff-accordion ${active ? "active" : ""}`}
      onClick={onClick}
    >
      <div className="staff-header">
        {ornumber}_{orname}_{orcity}
        {orrural}
        {oraddress}_{money}_{ordt}
      </div>
      {active && (
        <div className="staff-content">
          <ol>
            <li>:{ornumber}</li>
            <li>:{employeeid}</li>
            <li>:{handleOrderStatus(state)}</li>
            <li>:{ordt}</li>
            <li>:{ordd}</li>
          </ol>
          <ol>
            <li>:{uid}.{orname}</li>
            <li>:{oremail}</li>
            <li>:{orphone}</li>
            <li>:{money}</li>
            <li>:{pay ? "" : ""}</li>
            <li>
              :{orcity}
              {orrural}
              {oraddress}
            </li>
          </ol>
          <ol>
            <p>:({(efficiency + 0.0).toFixed(1)})
              <li className="staff-li-star">
                  <img
                    src="/images/staffInfo-star.png"
                    alt="star-y"
                    style={{ clipPath: `inset(0 ${100 - efficiency * 20}% 0 0)` }}
                  />
                <img
                  src="/images/staffInfo-star2.png"
                  alt="star-g"
                  style={{position:"absolute"}}
                />
              </li>
            </p>
            <p>:({(clean + 0.0).toFixed(1)})
              <li className="staff-li-star">
                  <img
                    src="/images/staffInfo-star.png"
                    alt="star-y"
                    style={{ clipPath: `inset(0 ${100 - clean * 20}% 0 0)` }}
                  />
                <img
                  src="/images/staffInfo-star2.png"
                  alt="star-g"
                  style={{position:"absolute"}}
                />
              </li>
            </p>
            <p>:({(careful + 0.0).toFixed(1)})
              <li className="staff-li-star">
                  <img
                    src="/images/staffInfo-star.png"
                    alt="star-y"
                    style={{ clipPath: `inset(0 ${100 - careful * 20}% 0 0)` }}
                  />
                <img
                  src="/images/staffInfo-star2.png"
                  alt="star-g"
                  style={{position:"absolute"}}
                />
              </li>
            </p>
            <p>:({(manner + 0.0).toFixed(1)})
              <li className="staff-li-star">
                <img
                  src="/images/staffInfo-star.png"
                  alt="star-y"
                  style={{ clipPath: `inset(0 ${100 - manner * 20}% 0 0)` }}
                />
                <img
                  src="/images/staffInfo-star2.png"
                  alt="star-g"
                  style={{position:"absolute"}}
                />
              </li>
            </p>
          </ol>
          <h6 style={{ overflowWrap: "break-word" }}>:<span className="fs-6">{reply}</span></h6>
        </div>
      )}
    </div>
  );
}

export default StaffAccordion;
