import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Swiper, SwiperSlide } from "swiper/react";
import StaffInfo from "./StaffInfo";
import "swiper/css";

const StaffInfos = () => {
  const [combinedData, setCombinedData] = useState([]);

  useEffect(() => {
    Promise.all([
      axios.get('http://localhost:4107/total/sta'),
      axios.get('http://localhost:4107/total/modal'),
      axios.get('http://localhost:4107/total/stars'),
    ])
      .then((responses) => {
        const cardData = responses[0].data; //API
        const modalData = responses[1].data;  //API
        const starsData = responses[2].data;  //API

        const combined = cardData.map((employee) => {
          //  modalData  employeeid
          const modalItem = modalData.find((modalEmployee) => modalEmployee.employeename === employee.employeename) || {};
          const starsItem = starsData.find((starsEmployee) => starsEmployee.employeeid === employee.employeeid) || {};

          return {
            ...employee,
            modalData: modalItem,
            total_efficiency: starsItem.total_stars
          };
        });

        setCombinedData(combined); //
      })
      .catch((err) => {
        console.error('no', err);
      });
  }, []);

  return (
    <div className="container">
      <div className="process-top staffInfo-title">
        <h3 className="title"> Staff evaluation</h3>
        <img src="images/flower.png" alt="" />
      </div>
      <Swiper
        loop={false}
        spaceBetween={0}
        slidesPerView={1}
        freeMode={true}
        breakpoints={{
          1200: {
            slidesPerView: 4,
          },
          992: {
            slidesPerView: 3,
          },
          768: {
            slidesPerView: 2,
          },
          576: {
            slidesPerView: 1,
          },
        }}
      >
        {combinedData.map((employee) => (
          <SwiperSlide key={employee.employeeid}>
            <StaffInfo
              name={employee.employeename}  //
              covid={employee.vaccine}      //
              star={employee.total_efficiency}  //
              number={employee.employeeid} //
              cases={employee.cases} //
              levle={employee.total_ratings} //
              img={employee.photo} 
              goodid={employee.goodid}  //
              racheck={employee.racheck} //
              reply={employee.modalData.reply} //modal 
              orname={employee.modalData.orname} //modal 
              clean={employee.modalData.e2} //modal 
              efficiency={employee.modalData.e1} //modal 
              careful={employee.modalData.e3} //modal 
              manner={employee.modalData.e4}  //modal 
              modalphoto={employee.modalData.photo}
              modalname={employee.modalData.employeename}
              stars={employee.total_efficiency} //modal
            />
          </SwiperSlide>
        ))}
      </Swiper>


    </div>
  );
};

export default StaffInfos;